"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import { Button } from "@/components/ui/button"

export default function RegisterPage() {
  const [name, setName] = useState("")
  const [gender, setGender] = useState("")
  const [age, setAge] = useState("")
  const [email, setEmail] = useState("")
  const [goals, setGoals] = useState("")
  const [submitted, setSubmitted] = useState(false)

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    // In a real app, send data to backend
    console.log({ name, gender, age, email, goals })
    setSubmitted(true)
    setName("")
    setGender("")
    setAge("")
    setEmail("")
    setGoals("")
  }

  return (
    <div className="min-h-screen bg-white flex items-center justify-center p-8">
      <Card className="max-w-lg w-full p-8">
        {submitted ? (
          <div className="text-center">
            <h2 className="text-2xl font-bold mb-4">Thank you for registering!</h2>
            <p>We will contact you soon to schedule your consultation.</p>
            <Button className="mt-6" onClick={() => setSubmitted(false)}>
              Register Another
            </Button>
          </div>
        ) : (
          <>
            <h1 className="text-3xl font-bold mb-6">Register for Consultation</h1>
            <form onSubmit={handleSubmit} className="space-y-6">
              <div>
                <label htmlFor="name" className="block mb-2 font-medium">Name</label>
                <Input
                  id="name"
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                  required
                />
              </div>
              <div>
                <label htmlFor="gender" className="block mb-2 font-medium">Gender</label>
                <select
                  id="gender"
                  value={gender}
                  onChange={(e) => setGender(e.target.value)}
                  className="w-full border border-gray-300 rounded px-3 py-2"
                  required
                >
                  <option value="">Select Gender</option>
                  <option value="male">Male</option>
                  <option value="female">Female</option>
                  <option value="other">Other</option>
                </select>
              </div>
              <div>
                <label htmlFor="age" className="block mb-2 font-medium">Age</label>
                <Input
                  id="age"
                  type="number"
                  min="1"
                  value={age}
                  onChange={(e) => setAge(e.target.value)}
                  required
                />
              </div>
              <div>
                <label htmlFor="email" className="block mb-2 font-medium">Email</label>
                <Input
                  id="email"
                  type="email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  required
                />
              </div>
              <div>
                <label htmlFor="goals" className="block mb-2 font-medium">Your Goals</label>
                <Textarea
                  id="goals"
                  value={goals}
                  onChange={(e) => setGoals(e.target.value)}
                  required
                />
              </div>
              <Button type="submit" className="w-full bg-black text-white hover:bg-gray-900">
                Submit Registration
              </Button>
            </form>
          </>
        )}
      </Card>
    </div>
  )
}
